import { MapItems, State } from "@/store/modules/delayCodes/state";
import { defineMutations } from "direct-vuex";
import json from "@/assets/data.json";
import {
  filterData,
  INDEX_CODE,
  INDEX_PROCESS,
  INDEX_REASON,
  INDEX_STAKEHOLDER,
  ListToBeDisplayed
} from "./utils";
import lunr from "lunr";

export enum MutationsTypes {
  SET_GROUPING_ID = "setGroupingId",
  SET_PROCESS_ID = "setProcessId",
  SET_REASON_ID = "setReasonId",
  SET_STAKEHOLDER_ID = "setStakeholderId",
  SET_SELECTED_LIST = "setSelectedList",
  RESET_SELECTION = "resetSelection",
  EXPERT_SET_CODE = "expertSetCode",
  SET_SEARCHED_TEXT = "setSearchedText"
}

function filterWithSearchText(
  resultsFromLunr: lunr.Index.Result[],
  resultsFromFilter: MapItems
): MapItems {
  const results: MapItems = {};
  resultsFromLunr.forEach(lunrResult => {
    if (resultsFromFilter[lunrResult.ref] !== undefined) {
      results[lunrResult.ref] = resultsFromFilter[lunrResult.ref];
    }
  });
  return results;
}

function updateFilters(state: State) {
  if (state.rawData === null) {
    state.rawData = json;
    const { groupings, processes, reasons, stakeholders } = filterData(
      state.rawData,
      null,
      null,
      null,
      null
    );
    state.allGroupings = groupings;
    state.allProcesses = processes;
    state.allReasons = reasons;
    state.allStakeholders = stakeholders;
  }
  // eslint-disable-next-line prefer-const
  let { groupings, processes, reasons, stakeholders } = filterData(
    state.rawData,
    state.selectedGroupingId,
    state.selectedProcessId,
    state.selectedReasonId,
    state.selectedStakeholderId
  );

  const searchedTextEnhaced = state.searchedText
    .trim()
    .split(" ")
    .map(it => (it !== "" ? `+${it}` : ""))
    .join(" ");
  const textSearchedResults = state.searchLunr.search(`${searchedTextEnhaced}`);
  switch (state.selectedListToDisplay) {
    case ListToBeDisplayed.Process:
      processes = filterWithSearchText(textSearchedResults, processes);
      break;
    case ListToBeDisplayed.Reason:
      reasons = filterWithSearchText(textSearchedResults, reasons);
      break;
    case ListToBeDisplayed.Stakeholder:
      stakeholders = filterWithSearchText(textSearchedResults, stakeholders);
      break;
    default:
      break;
  }
  state.groupings = groupings;
  state.processes = processes;
  state.reasons = reasons;
  state.stakeholders = stakeholders;
}

export const mutations = defineMutations<State>()({
  [MutationsTypes.SET_GROUPING_ID](state, payload: number | null) {
    state.selectedGroupingId = payload;
    updateFilters(state);
    if (
      Object.keys(state.processes).length === 0 ||
      Object.keys(state.reasons).length === 0 ||
      Object.keys(state.stakeholders).length == 0
    ) {
      state.selectedProcessId = null;
      state.selectedReasonId = null;
      state.selectedStakeholderId = null;
      updateFilters(state);
    }
  },
  [MutationsTypes.SET_PROCESS_ID](state, payload: number | null) {
    state.selectedProcessId = payload;
    updateFilters(state);
    if (
      Object.keys(state.reasons).length === 0 ||
      Object.keys(state.stakeholders).length == 0 ||
      Object.keys(state.groupings).length == 0
    ) {
      state.selectedReasonId = null;
      state.selectedStakeholderId = null;
      state.selectedGroupingId = null;
      updateFilters(state);
    }
    if (payload !== null) {
      state.searchedText = "";
      state.selectedListToDisplay = ListToBeDisplayed.Reason;
    }
  },
  [MutationsTypes.SET_REASON_ID](state, payload: number | null) {
    state.selectedReasonId = payload;
    updateFilters(state);
    if (
      Object.keys(state.processes).length === 0 ||
      Object.keys(state.stakeholders).length == 0 ||
      Object.keys(state.groupings).length == 0
    ) {
      state.selectedProcessId = null;
      state.selectedStakeholderId = null;
      state.selectedGroupingId = null;
      updateFilters(state);
    }
    if (payload !== null) {
      state.searchedText = "";
      state.selectedListToDisplay = ListToBeDisplayed.Stakeholder;
    }
  },
  [MutationsTypes.SET_STAKEHOLDER_ID](state, payload: number | null) {
    state.selectedStakeholderId = payload;
    updateFilters(state);
    if (
      Object.keys(state.processes).length === 0 ||
      Object.keys(state.reasons).length === 0 ||
      Object.keys(state.groupings).length == 0
    ) {
      state.selectedProcessId = null;
      state.selectedReasonId = null;
      state.selectedGroupingId = null;
      updateFilters(state);
    }
  },
  [MutationsTypes.SET_SELECTED_LIST](state, payload: number) {
    state.searchedText = "";
    state.selectedListToDisplay = payload;
    updateFilters(state);
  },
  [MutationsTypes.RESET_SELECTION](state) {
    state.selectedGroupingId = null;
    state.selectedProcessId = null;
    state.selectedReasonId = null;
    state.selectedStakeholderId = null;
    state.searchedText = "";
    state.selectedListToDisplay = ListToBeDisplayed.None;
    state.expertModeCodeIsInvalid = false;
    state.expertModeCode = "";
    updateFilters(state);
  },
  [MutationsTypes.EXPERT_SET_CODE](state, payload: string) {
    state.expertModeCode = payload;
    state.expertModeCodeIsInvalid = false;
    if (state.expertModeCode.length === 3) {
      let found = false;
      state.rawData?.data.forEach(item => {
        if (
          (item[INDEX_CODE] as string).toUpperCase() === payload.toUpperCase()
        ) {
          found = true;
          state.selectedProcessId = item[INDEX_PROCESS] as number;
          state.selectedReasonId = item[INDEX_REASON] as number;
          state.selectedStakeholderId = item[INDEX_STAKEHOLDER] as number;
        }
      });
      state.expertModeCodeIsInvalid = !found;
    }
  },
  [MutationsTypes.SET_SEARCHED_TEXT](state, payload: string) {
    if (state.searchedText !== payload) {
      state.searchedText = payload;
      updateFilters(state);
    }
  }
});
